































































































import Vue from 'vue';
import { search_kata, parse_tokens } from '@/service/kata';
import { calc_fitur, split_char } from '@/service/matriks_fitur';


export default Vue.extend({
  data: () => ({
    completions: [],
    keyword: '',
    tokens: [],
    has_result: false,
    char_idx: 0, 
    chars: [] as string[],
    char: '',
    char_headers: [] as string[],
    char_items: [] as any[]
  }),
  computed: {
    tokens_joined(): string {
      return this.tokens.map((it: any) => it.token).join('-');
    },
    fitur() {
      if (this.chars.length == 0) {
        return null;
      }
      const char = this.chars[this.char_idx];
      const char_fitur = calc_fitur(char);
      const char_headers = char_fitur.headers;
      const char_items = char_fitur.row;
      return {
        char,
        char_headers,
        char_items
      };
    }
  },
  methods: {
    async search(kata: string) {
      if (!kata || kata.length == 0) {
        return
      }
      const result = await search_kata(kata);
      this.completions = result.map((it: any) => ({
        value: it.word,
        text: it.word
      }));
    },
    async parse(kata: string) {
      if (!kata || kata.length == 0) {
        return
      }
      this.tokens = (await parse_tokens(kata)).tokens;
      const char_fitur = calc_fitur(kata[0]);
      this.chars = split_char(kata);
      this.char_idx = 0;
      this.has_result = true;
    },
    nextCharFitur() {
      if (this.char_idx >= this.chars.length - 1) {
        return;
      }
      this.char_idx += 1;
    },
    prevCharFitur() {
      if (this.char_idx <= 0) {
        return;
      }
      this.char_idx -= 1;
    }
  }
});
