const konsonan_headers = [
  'consonantal',
  'sonorant',
  'coronal',
  'anterior',
  'labial',
  'round',
  'distributed',
  'high',
  'low',
  'atr',
  'spread',
  'constricte',
  'lateral',
  'nasal',
  'strident',
  'del rel',
  'bilabial',
  'labiodental',
  'dental',
  'alveolar',
  'postalveolar',
  'palatal',
  'velar',
  'glottal'
];

export function split_char(s: string) {
  const gab_kons = /^(kh|ng|ny|sy|[bcdfghjklmnpqrstvwxyz])/;
  const v_pattern = /^[aiueo]/;
  const patterns = [gab_kons, v_pattern];

  let word = s;
  let result: string[] = [];
  while (word != '') {
    for (const pattern of patterns) {
      const is_match = word.match(pattern);
      if (is_match) {
        const match_w = is_match[0];
        word = word.slice(match_w.length);
        result.push(match_w);
      }
    }
  }
  return result;
}

export function calc_fitur(s: string) {
  if (!s || s.length == 0) {
    throw new Error('invalid s');
  }

  const v_pattern = /^[aiueo]/;
  
  if (s.match(v_pattern)) {
    const row = vocal[s];
    return {
      row,
      headers: vocal_headers
    };
  } else {
    const row = konsonan[s];
    return {
      row,
      headers: konsonan_headers
    };
  }
}